@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
nav {
  list-style-type: none;
  // height: 7.5vh;
  font-size: 2rem;
  position: fixed;
  width: 100%;
  z-index: 1000;
  transition: 0.2s;
  // box-shadow: 0px 5px 10px rgba(245, 186, 204, 0.459);
  font-family: 'Poppins', sans-serif;
}

ul {
  display: inline;
  margin-left: 7%;
}
nav ul li {
  display: inline-block;
  margin: 2rem 0 2rem 6rem;
}
.links {
  color: black;
  transition: 0.5s;
  font-weight: 300;
  letter-spacing: 1.3px;
}

.links:hover {
  color: black;
}
a:link {
  text-decoration: none;
  padding: 1px;
}

@media (max-width: 600px) {
  ul {
    text-align: center;
    margin-right: 6rem;
    margin-left: 0%;
  }
  nav ul li {
    margin: 2rem 1rem 0 1rem;
  }
  nav {
    height: 8.5vh;
  }
}

@media (max-width: 380px) {
  .links {
    letter-spacing: 0px;
  }
}
@media (max-width: 370px) {
  nav {
    font-size: 1.6rem;
  }
  nav ul li {
    margin: 0px;
    z-index: 5000;
    padding: 4px 11px;
    margin-left: -1rem;
    margin-right: -1rem;
  }
}
